<template>
        <div class="header">
            <h2>User Manager</h2>
        </div>
        <hr>
        <div class="body">
            <div class="actions">
               <div class="row">
                <div class="col-sm-1">
                    <label for="filter">Filter by</label>
                </div>
                <div class="col-1">
                   <select name="" id="" class="form-select-sm">
                          <option value="">All</option>
                          <option value="">Active</option>
                          <option value="">Inactive</option>
                   </select>
                </div>
                <div class="col-4"></div>
                <div class="col-3">
                    <input type="text" placeholder="Search ..." class="form-control" v-model="search" @input="searchUser" @change="searchUser" @click="searchUser">
                </div>
                <div class="col">
                  <p class="action-button">
                    <button class="btn btn-outline-primary " v-bind:title="'Create'" data-bs-toggle="modal" data-bs-target="#staticBackdrop" @click.prevent="getAllListId">
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-person-plus-fill" viewBox="0 0 16 16">
                            <path d="M1 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z"/>
                            <path fill-rule="evenodd" d="M13.5 5a.5.5 0 0 1 .5.5V7h1.5a.5.5 0 0 1 0 1H14v1.5a.5.5 0 0 1-1 0V8h-1.5a.5.5 0 0 1 0-1H13V5.5a.5.5 0 0 1 .5-.5z"/>
                          </svg>
                          Create
                    </button>
                    <button class="btn btn-outline-danger " v-bind:title="'Delete'" @click.prevent="getAllListId">
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-person-x-fill" viewBox="0 0 16 16">
                            <path fill-rule="evenodd" d="M1 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H1zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm6.146-2.854a.5.5 0 0 1 .708 0L14 6.293l1.146-1.147a.5.5 0 0 1 .708.708L14.707 7l1.147 1.146a.5.5 0 0 1-.708.708L14 7.707l-1.146 1.147a.5.5 0 0 1-.708-.708L13.293 7l-1.147-1.146a.5.5 0 0 1 0-.708z"/>
                          </svg>
                          Remove
                    </button>
                  </p>
                </div>
                <hr>
                <div class="table-responsive-sm">
                    <table class="table table-striped table-bordered table-hover">
                        <thead>
                            <th><input type="checkbox" @click="checkAll"></th>
                            <th>Code</th>
                            <th>Name</th>
                            <th>Username</th>
                            <th>Email</th>
                            <th>Mô tả</th>
                            <th>Tổ chức</th>
                            <th>Hành động</th>
                        </thead>
                        <tbody>
                           <tr v-for=" item in users"  :key="item.id">
                                <td class="col"  >
                                    <input type="checkbox" v-bind:value="item.id" v-model="ids" class="check">
                                </td>
                               
                                <th class="col">
                                    {{item.code}}
                                </th>
                                <td class="col">
                                    {{item.name}}
                                </td>
                                <td class="col">
                                    {{item.username}}
                                </td>
                                <td class="col">
                                    {{item.email}}
                                </td>
                                <td class="col">
                                    {{item.description == '' ? 'N/A' : item.description}}
                                </td>
                                <td class="col status">
                                    <span @click.prevent="obj=item" class="badge text-bg-success" data-bs-toggle="modal" data-bs-target="#detail">Chi tiết</span>
                                </td>
                                <td>
                                    <span>
                                        <button type="button" @click.prevent="setUser(item);id=item.id" class="btn btn-outline-primary" v-bind:title="'Edit'" data-bs-toggle="modal" data-bs-target="#update-modal">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-pen-fill" viewBox="0 0 16 16">
                                                <path d="m13.498.795.149-.149a1.207 1.207 0 1 1 1.707 1.708l-.149.148a1.5 1.5 0 0 1-.059 2.059L4.854 14.854a.5.5 0 0 1-.233.131l-4 1a.5.5 0 0 1-.606-.606l1-4a.5.5 0 0 1 .131-.232l9.642-9.642a.5.5 0 0 0-.642.056L6.854 4.854a.5.5 0 1 1-.708-.708L9.44.854A1.5 1.5 0 0 1 11.5.796a1.5 1.5 0 0 1 1.998-.001z"/>
                                            </svg>
                                        </button>
                                        <button class="btn btn-outline-danger" v-bind:title="'Delete'" @click.prevent="deleteById(item.id)">
                                            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-trash3" viewBox="0 0 16 16">
                                                <path d="M6.5 1h3a.5.5 0 0 1 .5.5v1H6v-1a.5.5 0 0 1 .5-.5ZM11 2.5v-1A1.5 1.5 0 0 0 9.5 0h-3A1.5 1.5 0 0 0 5 1.5v1H2.506a.58.58 0 0 0-.01 0H1.5a.5.5 0 0 0 0 1h.538l.853 10.66A2 2 0 0 0 4.885 16h6.23a2 2 0 0 0 1.994-1.84l.853-10.66h.538a.5.5 0 0 0 0-1h-.995a.59.59 0 0 0-.01 0H11Zm1.958 1-.846 10.58a1 1 0 0 1-.997.92h-6.23a1 1 0 0 1-.997-.92L3.042 3.5h9.916Zm-7.487 1a.5.5 0 0 1 .528.47l.5 8.5a.5.5 0 0 1-.998.06L5 5.03a.5.5 0 0 1 .47-.53Zm5.058 0a.5.5 0 0 1 .47.53l-.5 8.5a.5.5 0 1 1-.998-.06l.5-8.5a.5.5 0 0 1 .528-.47ZM8 4.5a.5.5 0 0 1 .5.5v8.5a.5.5 0 0 1-1 0V5a.5.5 0 0 1 .5-.5Z"/>
                                            </svg>
                                        </button>
                                    </span>
                                </td>
                           </tr>
                        </tbody>
                    </table>
                </div>
               </div>
            </div>
            {{id}}
        </div>
        <div class="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
            <div class="modal-dialog">
              <div class="modal-content">
                <div class="modal-header">
                  <h5 class="modal-title" id="staticBackdropLabel">Thêm mới</h5>
                  <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                </div>
                <div class="modal-body">
                  <form @submit.prevent="createUser()">
                    <div class="form-floating mb-3">
                        <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="user.username">
                        <label for="floatingInput">Username</label>
                    </div>
                    <div class="form-floating mb-3">
                        <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="user.password">
                        <label for="floatingInput">Password</label>
                    </div>
                    <div class="form-floating mb-3" >
                        <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="user.name">
                        <label for="floatingInput">Họ tên</label>
                    </div>
                    <div class="form-floating mb-3">
                        <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="user.email">
                        <label for="floatingInput">Email</label>
                    </div>
                    <div class="form-floating mb-3">
                        <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="user.description">
                        <label for="floatingInput">Mô tả</label>
                    </div>
                    <div class="form-floating mb-3">
                        <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="user.code">
                        <label for="floatingInput">Mã người dùng</label>
                    </div>
                    <div class="row">
                        <div class="col-3 m-0 p-0">
                            <button class="btn btn-outline-success flex-lg-column">Thêm mới</button>
                        </div>
                    </div>
                  </form>
                </div>
                <div class="modal-footer">
                  <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" id="close">Đóng</button>
                </div>
              </div>
            </div>
          </div>  
          <!-- Update 
            modal -->
          <div class="update-modal">
            <div class="modal fade" id="update-modal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
                <div class="modal-dialog">
                  <div class="modal-content">
                    <div class="modal-header">
                      <h5 class="modal-title" id="staticBackdropLabel">Cập nhật</h5>
                      <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                      <form @submit.prevent="updateById(id)">
                        <div class="form-floating mb-3">
                            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="updateUser.username" readonly>
                            <label for="floatingInput">Username</label>
                        </div>
                        <div class="form-floating mb-3">
                            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="updateUser.password" hidden>
                            <label for="floatingInput">Password</label>
                        </div>
                        <div class="form-floating mb-3" >
                            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="updateUser.name">
                            <label for="floatingInput">Họ tên</label>
                        </div>
                        <div class="form-floating mb-3">
                            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="updateUser.email">
                            <label for="floatingInput">Email</label>
                        </div>
                        <div class="form-floating mb-3">
                            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="updateUser.description">
                            <label for="floatingInput">Mô tả</label>
                        </div>
                        <div class="form-floating mb-3">
                            <input type="text" class="form-control" id="floatingInput" placeholder="" v-model="updateUser.code" readonly>
                            <label for="floatingInput">Mã người dùng</label>
                        </div>
                        <div class="row">
                            <div class="col-3 m-0 p-0">
                                <button class="btn btn-outline-success flex-lg-column">Cập nhật</button>
                            </div>
                        </div>
                      </form>
                    </div>
                    <div class="modal-footer">
                      <button type="button" class="btn btn-secondary" data-bs-dismiss="modal" id="close1">Đóng</button>
                    </div>
                  </div>
                </div>
              </div>
        </div>
        <!-- 
            Modal Detail 
         -->
  <div class="modal fade" id="detail" tabindex="-1" aria-labelledby="detail" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">Chi tiết</h5>
          <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
        </div>
        <div class="modal-body table-responsive-sm">
            <form  @submit.prevent="addOrganzation(obj.id)">
           <div class="row">
                <div class="col-7 mb-3">
                    <select name="" id="" class="form-select" v-model="oraId">
                        <option  selected>Danh sách tất cả tổ chức</option>
                        <option v-for="item in organizationss" :key="item" v-bind:value="item.oraId">{{item.name}}</option>
                    </select>
                </div>
           </div>
            <table class="table table-bordered table-hover">
                <thead>
                    <tr>
                        <th class="col">Id</th>
                        <th class="col">Username</th>
                        <th class="col">Tổ chức</th>
                    </tr>
                </thead>
                <tbody>
                    <tr>
                        <th >{{obj.id}}</th>
                        <td rowspan="3">{{obj.username}}</td>
                        <td >
                            <ul class="list-group list-group-flush">
                                <li class="list-group-item " v-for="item in obj.organizations" :key="item">
                                    {{item.name}}
                                </li>
                            </ul>
                            
                        </td>
                    </tr>
                </tbody>
            </table>
            <div class="row">
                <div class="col-4">
                    <button  class="btn btn-outline-success">Thêm tổ chức</button>
                </div>
            </div>
        </form>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-bs-dismiss="modal">Close</button>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>

table .col{
    padding-top: 1rem;
}
.status:hover{
    cursor: pointer;
}
.modal-backdrop, .modal-backdrop.fade.in{
    opacity: 0.1;
    }
</style>
<script>
import axios from 'axios'
export default {
    name: "UserManager",
    data() {
        return {
            users: [],
            user : {
                id: null,
                code: null,
                name: null,
                username: null,
                password : null,
                email: null,
                description: null
            },
            ids : [],
			updateUser : {
				id: null,
				code: null,
				name: null,
				username: null,
				password : null,
				email: null,
				description: null
			},search: '',
            id : '',
            obj : {},
            organizationss : [],
            u : {
                id : null,
                organizations :[]
            }

            ,oraId : ''
        }
    },
    created() {
        axios.defaults.headers.common['Authorization'] = "Bearer "+ localStorage.getItem('token');
        this.getUsers();
        this.getAllOrganization();
        this.getAccessToken();
    },
    methods: {
       async getUsers() {
            axios.get('/api/user/get')
                .then(response => {
                    this.users =  response.data
                    this.getAccessToken();
                })
                .catch(error => {
                    console.log(error)
                })
        },createUser(){
            axios.post('/api/user/save',this.user)
                .then(response => {
                    response.data
                    this.getUsers();
                    document.getElementById("close").click();
                    this.getAccessToken();
                })
                .catch(error => {
                    console.log(error)
                })
        },
        getAllListId(){
            let listCheckbox = document.querySelectorAll(".check");
            listCheckbox.forEach((item) => {
                if(item.checked){
                    this.ids.push(item.value);
                }
            })
            return this.ids;
        },checkAll(){
            var elements = document.querySelector("input[type='checkbox']");
            var listCheckbox = document.querySelectorAll(".check");
            if(elements.checked){
                listCheckbox.forEach((item) => {
                    item.checked = true;
                    
                })
            }else {
                listCheckbox.forEach((item) => {
                    item.checked = false;
                })
            }
        },deleteById(id){
            axios.delete('/api/user/delete/'+id)
                .then(response => {
                    response.data
                    this.getAccessToken();
                    this.getUsers();
                })
                .catch(error => {
                    console.log(error);
                    this.$toast.error('Permission denied');
                })
        },deleteAll(){
            this.getAllListId();
            axios.delete('/api/user/deleteAll/'+this.ids)
                .then(response => {
                    response.data
                    this.getUsers();
                    this.getAccessToken();
                })
                .catch(error => {
                    console.log(error)
                })
        },updateById(id){
            if(id  == null)
                throw Error("Not valid");
			axios.put('/api/user/update/'+id,this.updateUser)
				.then(response => {
					this.user = response.data
                    document.querySelector("#close1").click();
                    this.getAccessToken();
				})
				.catch(error => {
					console.log(error)
                    this.$toast.error('Permission denied');
				})
			},
            setUser(user){
                this.updateUser = user;
            },
            searchUser(){
                if(this.search == null || this.search == "" || this.search == undefined){
                    this.getUsers();
                }
                axios.get('/api/user/search/'+this.search)
                    .then(response => {
                        this.users =  response.data
                        this.getAccessToken();
                    })
                    .catch(error => {
                        console.log(error);
                        this.$toast.error('Permission denied');
                    })
            },getAllOrganization(){
                axios.get('/api/organization/get')
                    .then(response => {
                        this.organizationss =  response.data
                        this.getAccessToken();
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },async getOrganizationById(){
                axios.get('/api/organization/get/'+this.oraId)
                    .then( async response => {
                        this.u.organizations.push(response.data)
                        this.getAccessToken();
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },
             addOrganzation(id){
                this.getOrganizationById();
                alert(this.u.organizations);
                axios.post('/api/user/add-org/'+id,this.u.organizations).then(response => {
                    this.getUsers();
                    this.obj.organizations = response.data.organizations;
                    this.getAccessToken();
                }).catch(error => {
                    console.log(error)
                    this.$toast.error('Permission denied');
                })
            }, 
            async getAccessToken(){
                axios.post('/api/user/token/refresh')
                    .then( async response => {
                      localStorage.setItem('token',response.data.access_token);
                    })
                    .catch(error => {
                        console.log(error)
                    })
            },
    },
   
}
</script>